@keyframes fadeIn{from{opacity:0;transform:translateY(20px)}to{opacity:1;transform:translateY(0)}}article{animation:fadeIn .5s ease-out}article .relative::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(to bottom, transparent 50%, rgba(74, 74, 74, 0.1));opacity:0;transition:opacity .3s ease}article .relative:hover::after{opacity:1}article button{position:relative;overflow:hidden}article button::after{content:"";position:absolute;top:50%;left:50%;width:0;height:0;background:rgba(74,74,74,.1);border-radius:50%;transform:translate(-50%, -50%);transition:width .3s ease,height .3s ease}article button:hover::after{width:200%;height:200%}article button:active{transform:scale(0.98)}article button:disabled::after{display:none}article .line-clamp-2{display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden}.product-card{animation:fadeIn .5s ease-out;background-color:#fff;border-radius:8px;box-shadow:0 2px 8px rgba(0,0,0,.1);overflow:hidden;transition:transform .3s ease;flex:1 1 auto}.product-card:hover{transform:translateY(-5px)}.product-card__image{width:100%;height:200px;object-fit:cover}.product-card__content{padding:2rem}.product-card__title{color:#4a4a4a;font-size:1.2rem;margin-bottom:1rem}.product-card__price{color:#f0c0d0;font-size:1.5rem;font-weight:bold;margin-bottom:1.5rem}.product-card__rating{color:#6b6b6b;display:flex;align-items:center;gap:.5rem}.product-card__rating::before{content:"★";color:#f0e8f8}.product-card__button{width:100%;padding:1rem;background-color:#f0c0d0;color:#fff;border:none;border-radius:8px;cursor:pointer;transition:background-color .3s ease}.product-card__button:hover{background-color:#d4a0b0}