.team-member-card{background-color:#fff;border-radius:8px;overflow:hidden;box-shadow:0 2px 8px rgba(0,0,0,.1);transition:transform .3s ease,box-shadow .3s ease;display:flex;flex-direction:column;height:100%}.team-member-card:hover{transform:translateY(-5px);box-shadow:0 8px 16px rgba(0,0,0,.15)}.team-member-card__photo{width:100%;height:300px;object-fit:cover}.team-member-card__info{padding:1.5rem;flex-grow:1}.team-member-card__name{font-size:1.4rem;font-weight:600;color:#b8be9e;margin-bottom:.5rem}.team-member-card__specialization{font-size:1.1rem;color:#6b6b6b;margin-bottom:1rem}.team-member-card__experience{font-size:1rem;color:#6b6b6b;margin-bottom:1rem}.team-member-card__achievements{margin-top:1rem}.team-member-card__achievements h4{font-size:1.1rem;font-weight:600;color:#4a4a4a;margin-bottom:.5rem}.team-member-card__achievements ul{list-style:none;padding:0;margin:0}.team-member-card__achievements ul li{font-size:.95rem;color:#6b6b6b;margin-bottom:.3rem;position:relative;padding-left:15px}.team-member-card__achievements ul li::before{content:"•";position:absolute;left:0;color:#d4d9b9}