.service-card{animation:fadeIn .5s ease-out;background-color:#fff;border-radius:8px;box-shadow:0 2px 8px rgba(0,0,0,.1);overflow:hidden;transition:transform .3s ease;position:relative}.service-card:hover{transform:translateY(-5px);box-shadow:0 10px 15px -3px rgba(0,0,0,.1),0 4px 6px -2px rgba(0,0,0,.05)}.service-card .relative::after{display:none}.service-card .relative:hover::after{display:none}.service-card button{position:relative;overflow:hidden;z-index:1}.service-card button:active{transform:scale(0.98)}.service-card__header{padding:2rem;text-align:center;background:linear-gradient(to bottom, transparent 50%, rgba(74, 74, 74, 0.1))}.service-card__icon{font-size:2.5rem;margin-bottom:1.5rem}.service-card__title{color:#4a4a4a;font-size:1.5rem;margin-bottom:1rem}.service-card__description{color:#6b6b6b;margin-bottom:2rem;line-height:1.6}.service-card__features{padding:2rem;background-color:#f5f5f5}.service-card__features ul{list-style:none;padding:0;margin:0}.service-card__features li{color:#6b6b6b;padding:1rem 0;display:flex;align-items:center}.service-card__features li:before{content:"✓";color:#f0c0d0;margin-right:1rem}@keyframes fadeIn{from{opacity:0;transform:translateY(20px)}to{opacity:1;transform:translateY(0)}}