@import "@styles/variables.scss";
.service-modal-image {
  position: relative;
  height: 20rem;
  border-radius: 0.75rem;
  overflow: hidden;

  @media (min-width: 768px) {
    height: 100%;
    min-height: 24rem;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
} 